<template>
  <nav id='Navbar' class="navbar navbar-expand-lg navbar-dark bg-dark" role="navigation">
    <img class="rounded" src="../assets/Icon-App-29x29@1x.png" alt="FT logo square" /> 
    <router-link to="/" class="navbar-brand mr-100">Festival Applications</router-link>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="nav navbar-nav navbar-right w-100">
        <li v-if='isLoggedIn' class="nav-item">
          <router-link to="/dashboard" class="nav-link">{{ this.currentUser }}</router-link>
        </li>
        <li v-if="!isLoggedIn" class="nav-item">
          <router-link to="/login" class="nav-link">Login</router-link>
        </li>
        <li v-if="!isLoggedIn" class="nav-item">
          <router-link to="/register" class="btn btn-primary nav-link">Register</router-link>
        </li>
        <li v-if="isLoggedIn" class="nav-item">
          <div v-on:click="logout" class="nav-link">Logout</div>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import firebase from "firebase/app"

export default {
  name: 'navbar'
  , data() {
    return {
    }
  },
  computed: {
    isLoggedIn() {
      return this.$store.getters.isLoggedIn
    },
    currentUser() {
      return this.$store.getters.currentUser
    }
  },
  methods: {
    logout: function(e) {
      firebase
      .auth()
      .signOut()
      .then(() => {
        this.$store.dispatch('logout')
        this.$router.push('/')
      }).catch( err => {
        alert( err.message )
      } )
    }
  }
}
</script>
<style scoped>
img {
  radius-corners: 5px;
  padding-right: 1rem;
}
li {
  padding-right: 1rem;
  right:0;
}
</style>
