<template>
  <div id='login' class="row justify-content-md-center mt-5">
    <div class="col-sm-6 m-8">
      <div class="card text-white bg-secondary mb-3">
        <div class="card-header">
          Login
        </div>
        <div class="card-body">
          <div class="form-group">
            <label for="email">Email</label>
            <input type="email" id="email" class="form-control" v-model="email"/>
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input type="password" id="password" class="form-control" v-model="password"/>
          </div>
          <div class="row">
            <button v-if="!pending" v-on:click="login" class="btn btn-primary m-3">Login</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

import firebase from 'firebase/app'
export default {
  name:'login'
  , data() {
    return {
      email: ''
      , password: ''
    }
  },
  computed: {
    pending() {
      return this.$store.getters.pending
    }
  },
  methods: {
    login: function(e) {
      e.target.setAttribute('disabled', 'disabled')
      this.$store.dispatch('login')
      firebase.auth().signInWithEmailAndPassword( this.email, this.password )
        .then( response => {
          this.$store.dispatch('login_success', { email: this.email })
        .then(() => this.$router.push('/dashboard'))
        },
        err => {
          e.target.removeAttribute('disabled', 'disabled')
          alert( err.message )
        })
      e.preventDefault()
    }
  }
}
</script>
