<template>
  <div id="edit-act" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h3>Edit Act</h3>
      <form @submit.prevent="updateAct">
        <div class="form-group">
          <label>Artist/Band Name</label>
          <input 
            type="text" 
            name="name"
            class="form-control" 
            v-model="name" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
          <small
            v-show="errors.has('name')"
            class="text-danger"
            >
            <i class="fas fa-exclamation-circle"></i>
            {{ errors.first('name') }}
          </small>
        </div>
        <div class="form-group">
          <label>Where are you from</label>
          <input type="text" class="form-control" v-model="origin">
        </div>
        <div class="form-group">
          <label>Number of Band Members</label>
          <input type="text" class="form-control" v-model="noMembers">
        </div>
        <div class="form-group">
          <label>Music Genre</label>
          <input type="text" class="form-control" v-model="genre">
        </div>
        <div class="form-group">
          <label>Tell us about your most recent gigs and any festival appearances in 2018</label>
          <textarea 
            class="form-control" 
            v-model="recentGigs"
            ></textarea>
        </div>
        <div class="form-group">
          <label>Biography</label>
          <textarea 
            name="bio"
            class="form-control" 
            v-model="bio"
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            ></textarea>
          <small class="text-muted">To be used in promotional materials if selected.</small>
        </div>
        <div class="form-group">
          <label>Please check all dates that you are available</label>
          <div class="form-check">
            <input id="unplugged" type="checkbox" v-model="datesAvailable" value="Unplugged Saturday - 10 Feb 2018" class="form-check-input">
            <label for="unplugged" class="form-check-label">Unplugged Saturday - 10 Feb 2018</label>
          </div>
          <div class="form-check">
            <input id="fatTour"type="checkbox" v-model="datesAvailable" value="Fat Tuesday Tour - 13 Feb 2018" class="form-check-input">
            <label for="fatTour" class="form-check-label">Fat Tuesday Tour - 13 Feb 2018</label>
          </div>
        </div>
        <div class="card mt-1">
          <div class="card-header">
            <h4>Upload Images</h4>
            <button class="btn btn-primary" @click.prevent="onPickFile">Upload image</button>
            <input 
              type="file" 
              style="display:none" 
              ref="fileInput" 
              accept="image/*"
              @change="onFileChange" 
              multiple
            >
            <p class="card-text">
              <small class="muted-text">Only use .jpg files</small>
              <small>Images may take some time to render when first uploaded. Try reloading if you experience problems</small>
            </p>
          </div>
          <div 
             v-for="imageUrl in imageUrls"
             v-if="imageUrls" 
            >
              <img 
                :src="imageUrl" 
                height="100" 
                width="auto" 
                alt="uploaded image"
                />
            </div>
          <div class="row p-5 justify-content-md-center">
            <div 
               v-for="image in gallery"
               v-if="gallery" 
              >
              <div class="card mr-2 mb-2" >
                <img 
                  :src="'https://firebasestorage.googleapis.com/v0/b/fat-tuesday.appspot.com/o/uploads-output%2Fresized-thumb-' + image + '?alt=media'" 
                  height="100"
                  width="100"
                  class="card-image-top"
                  alt="uploaded image"
                  />
                <div class="card-body">
                  <div class="card-text">
                    <button @click.prevent="onDeleteImage( image )" class="btn btn-danger btn-block">Delete</button>
                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>
        <div class="card mt-3">
          <div class="card-header">
            <h4>Social Media links</h4>
          </div>
          <ul class="list-group list-group-flush">
            <li class="list-group-item">
              <div class="form-group">
                <label>Music Link <small>SoundCloud, BandCamp etc...</small></label>
                <input 
                  type="text" 
                  name="music"
                  class="form-control" 
                  v-model="musicLink"
                  v-validate="'url:require_protocol'"
                  data-vv-as="field"
                  data-vv-validate-on="change|load"
                  placeholder="Please include http/https"
                  />
                <small
                  v-show="errors.has('music')"
                  class="text-danger"
                  >
                  <i class="fas fa-exclamation-circle"></i>
                  {{ errors.first('music') }}
                </small>
                <small class="text-muted">Give us a link to the song you are most proud of, NOT your profile.</small>
              </div>
            </li>
            <li class="list-group-item">
              <div class="form-group">
                <label>Spotify Link</label>
                <input 
                  type="text" 
                  name="spotify"
                  class="form-control" 
                  v-model="spotifyLink"
                  v-validate="'url:require_protocol'"
                  data-vv-as="field"
                  data-vv-validate-on="change|load"
                  placeholder="Please include http/https"
                  />
                <small
                  v-show="errors.has('spotify')"
                  class="text-danger"
                  >
                  <i class="fas fa-exclamation-circle"></i>
                  {{ errors.first('spotify') }}
                </small>
              </div>
            </li>
            <li class="list-group-item">
              <div class="form-group">
                <label>Web address or Facebook Page</label>
                <input 
                  type="text" 
                  name="website"
                  class="form-control" 
                  v-model="webLink"
                  v-validate="'url:require_protocol'"
                  data-vv-as="field"
                  data-vv-validate-on="change|load"
                  placeholder="Please include http/https"
                  />
                <small
                  v-show="errors.has('website')"
                  class="text-danger"
                  >
                  <i class="fas fa-exclamation-circle"></i>
                  {{ errors.first('website') }}
                </small>
              </div>
            </li>
            <li class="list-group-item">
              <div class="form-group">
                <label>Twitter Link</label>
                <input 
                  type="text" 
                  name="twitter"
                  class="form-control" 
                  v-model="twitterLink"
                  v-validate="'url:require_protocol'"
                  data-vv-as="field"
                  data-vv-validate-on="change|load"
                  placeholder="Please include http/https"
                  />
                <small
                  v-show="errors.has('twitter')"
                  class="text-danger"
                  >
                  <i class="fas fa-exclamation-circle"></i>
                  {{ errors.first('twitter') }}
                </small>
              </div>
            </li>
            <li class="list-group-item">
              <div class="form-group">
                <label>YouTube / Vimeo Link</label>
                <input 
                  type="text" 
                  name="video"
                  class="form-control" 
                  v-model="videoLink"
                  v-validate="'url:require_protocol'"
                  data-vv-as="field"
                  data-vv-validate-on="change|load"
                  placeholder="Please include http/https"
                  />
                <small
                  v-show="errors.has('video')"
                  class="text-danger"
                  >
                  <i class="fas fa-exclamation-circle"></i>
                  {{ errors.first('video') }}
                </small>
                <small class="text-muted">Give us a link to a music video from your band if you have one</small>
              </div>
            </li>
          </ul>
        </div>
        <div class="alert alert-danger mt-3" role="alert">
          <h4 class="alert-heading">Impotant!</h4>
          <p>We will not be able to consider your application unless at least one of the social media links above is completed</p>
          <hr>
          <small>The information in this form will be used on the website and festival app. The more information provided the better your entry.</small>
        </div>

        <div class="mt-3">
          <button class="btn btn-lg btn-block btn-primary pr-sm">Submit</button>
          <router-link to="/" class="btn btn-lg btn-block btn-secondary">Cancel</router-link>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'edit-act'
  , data() {
    return {
      name: null
      , act_id: null
      , origin: null
      , noMembers: null
      , genre: null
      , recentGigs: null
      , bio: null
      , datesAvailable: []
      , upload: null
      , images: []
      , imageUrls: []
      , gallery: []
      , musicLink: null
      , spotifyLink: null
      , webLink: null
      , twitterLink: null
      , videoLink: null
    }
  },
  beforeRouteEnter( to, from, next ) {
    db.collection('acts')
    .doc( to.params.act_id )
    .onSnapshot( doc => {
      var data = doc.data()
      if( firebase.auth().currentUser.email == data.owner ) {
        next( vm => {
          vm.name = data.name
          vm.origin = data.origin
          vm.noMembers = data.noMembers 
          vm.genre = data.genre 
          vm.recentGigs = data.recentGigs 
          vm.bio = data.bio 
          vm.datesAvailable = data.datesAvailable 
          vm.gallery = data.gallery 
          vm.musicLink = data.musicLink 
          vm.spotifyLink = data.spotifyLink 
          vm.webLink = data.webLink 
          vm.twitterLink = data.twitterLink 
          vm.videoLink = data.videoLink 
        } )
      } else {
        alert( 'You do not have access to this act' )
        next( {
          path: '/acts/list'
          , query: {
            redirect: to.fullPath
          }
        } )
      }
    } )
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    onFileChange ( e ) {
      const files = e.target.files
      for ( var i=0;i<files.length;i++ ) {
        const fileReader = new FileReader()
        let filename = files[i].name
        if ( filename.lastIndexOf('.')  <=0 ) return alert( "please use a valid file" )
        fileReader.addEventListener( 'load', () => {
          this.imageUrls.push(fileReader.result) 
        } )
        fileReader.readAsDataURL( files[i] )
        this.images.push( files[i] )
      }
    },
    onDeleteImage ( url ) {
      this.gallery.splice(this.gallery.indexOf(url), 1)
      db.collection('acts')
      .doc( this.$route.params.act_id )
      .update( {
        gallery: this.gallery
      } )
      .then( () => {
        const storage = firebase.storage()
        storage.ref('uploads/' + url)
        .delete()
        .then(() => {
          return
        })
        .catch( error => alert( error ) )
      } )
      .catch( error => alert( error ) )

    },
    onPickFile () {
      this.$refs.fileInput.click()
    }, 
    fetchData() {
      db.collection('acts')
      .doc( this.$route.params.act_id )
      .onSnapshot( doc => {
        const data = doc.data()
        this.name = data.name
        this.origin = data.origin
        this.noMembers = data.noMembers
        this.genre = data.genre 
        this.recentGigs = data.recentGigs 
        this.bio = data.bio 
        this.datesAvailable = data.datesAvailable 
        this.gallery = data.gallery 
        this.musicLink = data.musicLink 
        this.spotifyLink = data.spotifyLink 
        this.webLink = data.webLink 
        this.twitterLink = data.twitterLink 
        this.videoLink = data.videoLink 
      } )
    },
    updateAct() {
      let updatedAt = new Date()
        , act_id = this.$route.params.act_id
        , ref = db.collection('acts').doc( act_id )
      this.$validator.validate()
      .then( result => {
        if(!result) {
          return alert('Please fill in the form as required')
        }
        ref.update( {
          name: this.name
          , origin: this.origin
          , noMembers: this.noMembers
          , genre: this.genre 
          , recentGigs: this.recentGigs 
          , bio: this.bio 
          , datesAvailable: this.datesAvailable 
          , musicLink: this.musicLink 
          , spotifyLink: this.spotifyLink 
          , webLink: this.webLink 
          , twitterLink: this.twitterLink 
          , videoLink: this.videoLink 
          , updatedAt: updatedAt
        } )
        .then( () => {
          const storage = firebase.storage()
          let filenames = []
            , i = 0
          this.images.forEach( image => {
            let filename = act_id
              , ext = image.type.split('/')[1]
            i++
            filename += '-'
            filename += Date.now()
            filename += '-'
            filename += i
            filename += '.' + ext
            filenames.push( filename )
            storage.ref('uploads/' + filename).put(image)
          } )
          return Promise.all( filenames )
        } )
        .then( fileData => {
          var combined = [ ...this.gallery, ...fileData ]
          this.gallery.concat( fileData )
          return ref.update( {
            gallery: combined
          } )
        } )
        .then( () => {
          this.$router.push( { 
            name: 'view-act'
            , params: act_id } )
        } )
        .catch( error => alert( error ) )
      } )
    }
  }
}
</script>
