<template>
  <div id="dashboard" class="row justify-content-md-center mt-5">
    <div class="col-sm-6">
      <h3>My Applications</h3>
      <div class="list-group mt-3 mb-3">
        <router-link 
          v-for="act in acts" 
          v-bind:key="act.id" 
          v-bind:to="{ name:'view-act', params:{ act_id: act.id } }" 
          class="list-group-item list-group-item-action">
          {{ act.name }}
          {{ act.createdAt }}
        </router-link>
      </div>
      <router-link 
        to="/acts/new" 
        class="btn btn-secondary">
        New Application
      </router-link>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'dashboard'
  , data() {
    return {
      acts: []
    }
  }, 
  created() {
    var owner = firebase.auth().currentUser.email
    db.collection('acts')
    .where( 'owner', '==', owner )
    .orderBy('name')
    .get()
    .then(qs => { 
      qs.forEach( doc => { 
        const data = {
          'id':doc.id
          , 'name':doc.data().name
          , 'description':doc.data().description
        }
        this.acts.push( data )
      } ) 
    } )
  }
}
</script>

