<template>
  <div class="row justify-content-md-center mt-5">
    <div class="col-sm-6">
      <div id="Register">
        <div class="card text-white bg-primary mb-3">
          <div class="card-header">
            Register
          </div>
          <div class="card-body">
            <div class="form-group">
              <label for="email">Email</label>
              <input 
                type="email" 
                id="email" 
                name="email"
                class="form-control" 
                v-model="email"
                v-validate="'required|email'"
                />
              <small
                v-show="errors.has('email')"
                class="text-light"
                >
                <i class="fas fa-exclamation-circle"></i>
                {{ errors.first('email') }}
              </small>
            </div>
            <div class="form-group">
              <label for="telephone">Telephone</label>
              <input 
                type="telephone" 
                id="telephone" 
                name="telephone"
                class="form-control" 
                v-model="telephone"
                />
              <small class="white">We will only use this for clarification</small>
            </div>
            <div class="form-group">
              <label for="password">Password</label>
              <input 
                type="password" 
                id="password" 
                name="password" 
                class="form-control" 
                v-model="password"
                v-validate="'required|min:6|alpha_num'"
                />
              <span
                v-show="errors.has('password')"
                class="text-light"
                >{{ errors.first('password') }}</span>
            </div>
            <div class="row">
              <button v-on:click="register" class="btn btn-bg-light m-3">Register</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'Register'
  , data() {
    return {
      email: ''
      , telephone: ''
      , password: ''
      , access: ''
    }
  },
  methods: {
    register: function(e) {
      e.target.setAttribute('disabled', 'disabled')
      firebase.auth().createUserWithEmailAndPassword( this.email, this.password )
      .then( response => {
        db.collection('users')
        .add( {
          email: this.email
          , telephone: this.telephone
          , access: 'user'
        } )
        .then( () => {
          this.$store.dispatch('login_success', { email: this.email })
          this.$router.push('/dashboard')
        } )
        .catch( error => alert( error ) )
      },
      err => {
        e.target.removeAttribute('disabled', 'disabled')
        alert( err.message )
      })
      e.preventDefault()
    }
  }
}
</script>
