<template>
  <div id="listActs">
    <h1>List Acts</h1>
    <div class="list-group">
      <router-link v-for="act in acts" v-bind:key="act.id" v-bind:to="act.id" class="list-group-item list-group-item-action">{{ act.name }}</router-link>
    </div>
    <router-link to="/acts/new" class="btn btn-secondary">Add Act</router-link>
  </div>
</template>
<script>
import db from '../lib/firebaseInit'
export default {
  name:'listActs'
  , data() {
    return {
      acts: []
    }
  }, 
  created() {
    db.collection('acts')
    .orderBy('name')
    .get()
    .then(qs => { 
      qs.forEach( doc => { 
        const data = {
          'id':doc.id
          , 'name':doc.data().name
          , 'description':doc.data().description
        }
        this.acts.push( data )
      } ) 
    } )
  }
}
</script>
