            required
            ></textarea>
          <small class="text-muted">To be used in promotional materials if selected.</small>
        </div>
        <div class="form-group">
          <label>Please check all dates that you are available</label>
          <div class="form-check">
            <input 
              id="unplugged" 
              type="checkbox" 
              v-model="datesAvailable" 
              value="Unplugged Saturday - 10 Feb 2018" 
              class="form-check-input"
              />
            <label for="unplugged" class="form-check-label">Unplugged Saturday - 10 Feb 2018</label>
          </div>
          <div class="form-check">
            <input 
              id="fatTour"type="checkbox" 
              v-model="datesAvailable" 
              value="Fat Tuesday Tour - 13 Feb 2018" 
              class="form-check-input"
              />
            <label for="fatTour" class="form-check-label">Fat Tuesday Tour - 13 Feb 2018</label>
          </div>
        </div>
        <div class="card mt-1">
          <div class="card-header">
