          </ul>
        </div>
        <div class="alert alert-danger mt-3" role="alert">
          <h4 class="alert-heading">Impotant!</h4>
          <p>We will not be able to consider your application unless at least one of the social media links above is completed</p>
          <hr>
          <small>The information in this form will be used on the website and festival app. The more information provided the better your entry.</small>
        </div>

        <div class="mt-3">
          <button class="btn btn-lg btn-block btn-primary pr-sm">Submit</button>
          <router-link to="/" class="btn btn-lg btn-block btn-secondary">Cancel</router-link>
